import { SHOP_DETAIL } from '@/config/router.js'
import { debounce } from "@/utils/util.js";

export default {
	props: {
		shopList: {
			type: Array
		}
	},
	data() {
		return {
			searchValue: ""
		}
	},
	computed: {
		handSearchValueThrottl() {
			return debounce(this.handSearchValue, 1000);
		}
	},
	methods: {
		goShopDetail: function(id) {
			uni.navigateTo({
				url: `${SHOP_DETAIL}?id=${id}`
			});
		},
		handSearchValue() {
			this.$emit("searchChange", this.searchValue);
		}
	}
}
